.title-wrapper {
  text-align: center; }
  .title-wrapper .title {
    font-size: 2.5rem;
    display: inline-block;
    position: relative; }
  .title-wrapper.both-line-title .title {
    padding: 0 60px; }
  .title-wrapper.right-line-title .title {
    padding-right: 60px; }
  .title-wrapper.left-line-title .title {
    padding-left: 60px; }
  .title-wrapper.both-line-title .title:before, .title-wrapper.both-line-title .title:after, .title-wrapper.right-line-title .title:after, .title-wrapper.left-line-title .title:before {
    content: '';
    display: block;
    background: #222;
    height: 2px;
    width: 40px;
    top: 50%;
    position: absolute;
    margin-top: -1px; }
  .title-wrapper.both-line-title .title:before, .title-wrapper.left-line-title .title:before {
    left: 0; }
  .title-wrapper.both-line-title .title:after, .title-wrapper.right-line-title .title:after {
    right: 0; }
  .title-wrapper.right-line-title, .title-wrapper.left-line-title {
    text-align: left; }
